* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  outline: none;
  border: 0;
  text-decoration: none;
  list-style: none;
}

html,
body,
.wrapper {
  height: 100%;
  background: #333;
}

.wrapper {
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}

main {
  flex: auto;
}

[class*=__container] {
  max-width: "container size + 30px";
  margin: 0 auto;
  padding: 0px 15px;
}